*{
  color: aquamarine;
  background-color: rgb(0, 0, 0);
  font-size: large;
  font-family: 'Times New Roman', Times, serif;
}

body {
  padding: 60px;
}

input{
  font-size: 20px;
  width: 100%;
}

.card-grid{
  display: flex;
  flex-direction: row;
  flex-wrap: row;
}


.card {
  align-content: center;
  border: 1px solid gray;
  border-radius: 6px;
  overflow: hidden;
  margin-bottom: 10px;
  margin-right: 10px;
  
}

.card p{
  padding: 5px;
  text-align: center;
}

.card img{
  max-height: 170px ;
}